@import url(https://fonts.googleapis.com/css?family=Krub|Niramit);
@media screen {

    .modalContainer {
        margin: 8px;
    }

    .modalTextContainer {
        display: flex;
        align-items: flex-start;
    }

    .modalTitle {
        font-size: 16px;
        padding: 10px;
        color: orange;
        font-weight: 700;
    }

}

.hide {
    display: none !important;
}

.globalAlert {
    position: absolute;
    border: 1px solid #000;
    border-radius: 4px;
    color: #42a94c;
    background-color: #fff;
    top: 38vh;
    width: 500px;
    left: 50%;
    margin-left: -250px;
    z-index: 2;
}

.alertButton {
    background-color: #ef5858;
}

.alertButton:hover {
    background-color: red;
}

.successButton {
    background-color: #1ab394;
}

.successButton:hover {
    background-color: #028a6f;
}

.buttonsInModalContainer {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    margin: 15px 20px;
}

.buttonsInModal {
    padding: 10px 10px;
    color: #fff;
    margin: 0 5px;
    cursor: pointer;
}
body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }

  .hide {
    display: none;
  }

  .overlay {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: black;
    opacity: 0.5;
    z-index: 1;
}
  
  .square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 64px;
    font-weight: bold;
    line-height: 34px;
    height: 94px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 94px;
  }

  .square.highlight {
    background-color: #ddd;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }

  .main-container {
    display: flex;
    flex-direction: row;
  }
  
  .game {
    display: flex;
    flex-direction: row;
  }
  
  .game-info {
    margin-left: 20px;
  }

  .input-squares-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .input-squares {
    margin: 0px 80px;
    padding: 6px;
  }

  .input-squares::-webkit-input-placeholder {
    color: #999;
  }

  .input-squares:-ms-input-placeholder {
    color: #999;
  }

  .input-squares::-ms-input-placeholder {
    color: #999;
  }

  .input-squares::placeholder {
    color: #999;
  }

  .danger-span {
    color: red;
  }
